<template>
  <div class="hello">
    <el-row>
      <!-- 左侧空白 -->
      <el-col :span="3"><div class="grid-content"></div></el-col>

      <el-col :span="18">
        <el-container>
          <!-- 头部 -->
          <el-header>
            <h1>
              查询
            </h1>
          </el-header>

          <!-- 主部 -->
          <el-container>
            <!-- 左侧 -->
            <el-aside width="200px">Aside</el-aside>
            <!-- 主部 -->
            <el-main>
              <div class="grid-content form-style">
                <!-- 表单开始 -->
                <el-form ref="form" :model="form" label-width="100px" method="POST" action="http://localhost:5000">
                  <h1>
                    请选择婴儿
                  </h1>
                  <!-- 性别选择 -->
                  <el-form-item label="性别">
                    <el-radio-group v-model="form.gender">
                      <el-radio-button label="男孩" name="boy"></el-radio-button>
                      <el-radio-button label="女孩" name="girl"></el-radio-button>
                    </el-radio-group>
                  </el-form-item>
                  <!-- 出生日期选择 -->
                  <el-form-item label="出生日期">
                    <el-date-picker
                      v-model="form.datevalue"
                      align="right"
                      type="date"
                      placeholder="选择日期"
                      :picker-options="pickerOptions">
                    </el-date-picker>
                  </el-form-item>

                  <el-form-item>
                    <el-button type="primary" @click="OnSubmit(form)" value="submit">查询</el-button>
                    <el-button @click="resetForm('form')">重置</el-button>
                  </el-form-item>
                </el-form>
                <!-- 表单结束 -->

                <div class="grid-content">
                  查询结果
                  <p>
                    {{message}}
                  </p>
                </div>
              </div>
            </el-main>
          </el-container>

        </el-container>
      </el-col>

      <!-- 右侧空白 -->
      <el-col :span="3"><div class="grid-content"></div></el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      message: 'ss',
      form: {
        gender: '1',
        datevalue: ''
      },
      pickerOptions: {
        disabledDate (time) {
          return ( time.getTime() > Date.now() ) || ( time.getTime() + 3600 * 1000 * 24 * 365 * 8 < Date.now() )
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      }
    }
  },
  methods: {
    OnSubmit (form) {
      this.message = ['sd']
      this.axios({
        method: 'post',
        url: '/formdata',
        data: form
      })
      .then((response) => {
        console.log(response)
      })
      .catch((error) => {
        console.log(error)
        })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-row {
    margin-bottom: 20px;
  }
  .grid-content {
    min-height: 36px;
  }
  .form-style {
    padding-left: 40px;
    text-align: left;
    border-radius: 8px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    padding-top: 40px;
    height: 500px;
  }
  .el-col {
    border-radius: 4px;
  }
  h1, h2 {
    font-weight: normal;
  }

  .el-header {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    /* line-height: 60px; */
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    /* background-color: #E9EEF3; */
    color: #333;
    text-align: center;
    height: 600px;
    /* line-height: 60px; */
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 60px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 120px;
  }
</style>



